body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Ensure body and app root container truly cover 100% of the screen and that the body text is our selected font unless we change it specifically */

body {
  width: 100%;
  margin: 0;
  font-family: 'Roboto', sans-serif, sans-serif, arial;
  font-family: var(--font), sans-serif, arial;
}

/* Make sure React base element takes full width and height */

#root {
  position: absolute;
  width: 100%;
  height: 100%;
}


/* Remove decoration from links */

a:link,
a:visited,
a:hover,
a:active {
  text-decoration: none;
  color: inherit;
}


/* Add Theme Variables here (like colors and fonts we will use throughout the project) */

:root {
  --font: 'Roboto', sans-serif;
  --font-header: 'Roboto Condensed', sans-serif;
  --color-yellow: #FFED5C;
  --color-orange: #FFA324;
  --color-red: #FF4A24;
  --color-gray: #2D353D;
  --color-lGray: #47535E;
}

/* COMMON STYLES */

/* Flex-Box - Centered Column */
.fbc {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

/* pad:0 mar:0 */
.pm0 {
  padding: 0;
  margin: 0;
}

/* Pointer hand for links */
.pointy {
  cursor: pointer;
}


/* Setup Grid on top level div. This sets a space for the Nav and Footer and allows the center portion (1fr = all remaining space) to be used by whichever component is being shown. */

#top-level-container {
  display: grid;
  grid-template-columns: [c1] 100% [c2];
  grid-template-rows: [r1] 100px [r2] 1fr [r3] 50px [r4];
  width: 100%;
  height: auto;
  margin: 0;
  padding: 0;
}

/* FOOTER */
footer {
  grid-column: c1 / c2;
  grid-row: r3 / r4;
  justify-content: flex-start;
  width: 100%;
  margin-top: 1em;
  background-color: #2D353D;
  background-color: var(--color-gray);
  color: #FFA324;
  color: var(--color-orange);
  border-top: 3px solid #000;
}

.footer-list {
  list-style: none;
}

#results {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 2em auto;
  width: 100%;
  max-width: 600px;
  min-height: calc(100vh - 200px);
}
.res__btn,
.res__header,
.res__container {
  width: 85%;
  border-radius: 10px;
  box-shadow: 0 -1px 12px var(--color-gray);
  background-color: var(--color-gray);
  background-image:
  url(https://www.transparenttextures.com/patterns/always-grey.png),
  -webkit-gradient(
  linear,
  right bottom, left top,
  from(var(--color-gray)),
  color-stop(90%, var(--color-lGray))
  );
  background-image:
  url(https://www.transparenttextures.com/patterns/always-grey.png),
  -webkit-linear-gradient(
  bottom right,
  var(--color-gray),
  var(--color-lGray) 90%
  );
  background-image:
  url(https://www.transparenttextures.com/patterns/always-grey.png),
  linear-gradient(
  to top left,
  var(--color-gray),
  var(--color-lGray) 90%
  );
}
.res__wrapper {
  display: flex;
  flex-direction: row;
  height: 400px;
  justify-content: space-evenly;
  color: #fff;
}
.res__bar {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.res__bar_names {
  margin-bottom: 0.5em;
  text-align: center;
  font-size: 1.5em;
  font-weight: bold;
}
.res__title {
  font-size: 2em;
  color: var(--color-yellow);
  text-align: center;
  margin: 25px;
}

@-webkit-keyframes first-place {
  from {
    height: 0px;
  }
  to {
    height: 350px;
  }
}

@keyframes first-place {
  from {
    height: 0px;
  }
  to {
    height: 350px;
  }
}
@-webkit-keyframes second-place {
  from {
    height: 0px;
  }
  to {
    height: 250px;
  }
}
@keyframes second-place {
  from {
    height: 0px;
  }
  to {
    height: 250px;
  }
}
@-webkit-keyframes third-place {
  from {
    height: 0px;
  }
  to {
    height: 150px;
  }
}
@keyframes third-place {
  from {
    height: 0px;
  }
  to {
    height: 150px;
  }
}
.res__bar-inner {
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  width: 80px;
}
.res__bar:first-child .res__bar-inner {
  height: 250px;
  -webkit-animation-name: second-place;
          animation-name: second-place;
  background-color: #a9c4ea;
}
.res__bar:nth-child(2) .res__bar-inner {
  height: 350px;
  -webkit-animation-name: first-place;
          animation-name: first-place;
  background-color: #b8e0a4;
}
.res__bar:last-child .res__bar-inner {
  height: 150px;
  -webkit-animation-name: third-place;
          animation-name: third-place;
  background-color: #f09c99;
}
.res__btn {
  font-size: 1.6em;
  padding: 0;
  margin: 1.5em auto;
  color: var(--color-orange);
  background-color: var(--color-gray);
  background-image:
  url(https://www.transparenttextures.com/patterns/always-grey.png),
  -webkit-gradient(
  linear,
  right bottom, left top,
  from(var(--color-gray)),
  color-stop(90%, var(--color-lGray))
  );
  background-image:
  url(https://www.transparenttextures.com/patterns/always-grey.png),
  -webkit-linear-gradient(
  bottom right,
  var(--color-gray),
  var(--color-lGray) 90%
  );
  background-image:
  url(https://www.transparenttextures.com/patterns/always-grey.png),
  linear-gradient(
  to top left,
  var(--color-gray),
  var(--color-lGray) 90%
  );
  border: none;
}

@media only screen and (min-width: 600px) {

  .res__bar,
  .res__bar-inner {
      width: 120px;
  }

}



