}

.steps-list {
  font-size: 1.2em;
  list-style-type: upper-roman;
}

.step-instructions {
  font-family: var(--font-header);
}

.step__ol {
    counter-reset: leaderboard;
    padding-left: 0;
    list-style: none;
    width: 65%;
    margin-bottom: 2em;
}

.step__li {
    position: relative;
    font-size: 1.5em;
    background-color: #fff;
    padding: 18px 10px 18px 50px;
    margin: 0.2em 0;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    border-radius: 10px;
    counter-increment: leaderboard;
}

.step__li::before {
    content: counter(leaderboard);
    position: absolute;
    z-index: 2;
    top: 5px;
    left: 15px;
    width: 25px;
    height: 25px;
    line-height: 25px;
    color: var(--color-orange);
    background: var(--color-gray);
    border-radius: 20px;
    text-align: center;
}

.step__li mark {
    position: absolute;
    z-index: 2;
    top: -15px;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 18px 10px 18px 50px;
    margin: 0;
    background: none;
    font-family: var(--font-header);
}

.join-board {
    margin-top: 20px;
}

